<script>
import 'swiper/dist/js/swiper'
import 'swiper/dist/css/swiper.css'
import Swiper from 'swiper'

export default {
    name: "account",
    components: {

    },
    data() {
        return {
            selectedIndex: '',
            priceValue: null,
            priceList: [
                {
                    id: 1,
                    price: 500
                },
                {
                    id: 2,
                    price: 1000
                },
                {
                    id: 3,
                    price: 1500
                },
                {
                    id: 4,
                    price: 2000
                },
            ],
            standardList: [
                {
                    id: 1,
                    day: "3",
                    discount: "9.5",
                    dayText: "天",
                    className: "black black1"
                }, {
                    id: 2,
                    day: "7",
                    discount: "9",
                    dayText: "天",
                    className: "black black2"
                }, {
                    id: 3,
                    day: "1",
                    discount: "8",
                    dayText: "个月",
                    className: "white white1"
                }, {
                    id: 4,
                    day: "3",
                    discount: "7.5",
                    dayText: "个月",
                    className: "white white2"
                }, {
                    id: 5,
                    day: "1",
                    discount: "6.5",
                    dayText: "年",
                    className: "white white3"
                }
            ]
        }
    },
    methods: {
        Pagepush() {
            this.$router.push('Service_en')
        },
        selctedPriceHandler(item) {
            this.selectedIndex = item.id
        },
        less() {
            if (!this.priceValue) return
            this.priceValue = Number(this.priceValue) - 1
        },
        add() {
            this.priceValue = Number(this.priceValue) + 1
        },
        initSwiper() {
            new Swiper('.swiper-container1', {
                effect: 'coverflow',
                autoplay: {
                    delay: 3000,
                    stopOnLastSlide: false,
                    disableOnInteraction: false
                },
                grabCursor: true,
                centeredSlides: true,
                slidesPerView: 'auto',
                spaceBetween: 30,
                initialSlide: 2,
                coverflowEffect: {
                    rotate: 0,
                    stretch: 0,
                    depth: 100,
                    modifier: 0.8,
                    slideShadows: true,
                    scale: 0.8
                },
                loop: true,
                navigation: {
                    nextEl: '.swiper-button-next',
                    prevEl: '.swiper-button-prev',
                },
                // 延迟加载
                lazy: {
                    loadPrevNext: true
                },
                observer: true, //修改swiper自己或子元素时，自动初始化swiper
                observeParents: true //修改swiper的父元素时，自动初始化swiper
            })
            document.querySelectorAll('.swiper-slide-shadow-left').forEach(item => {
                item.style = 'background-image:none;'
            })
            document.querySelectorAll('.swiper-slide-shadow-right').forEach(item => {
                item.style = 'background-image:none;'
            })
        },
    },
    mounted() {
        this.$router.afterEach((to, from, next) => {
            window, scrollTo(0, 0)
        })
        this.initSwiper()
    },
}
</script>

